import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

/**
 * 作用域：整个SPA应用生命周期
 * 作用：全局状态存储
 */
export const store = new Vuex.Store({
  state:{
    test:{
      value:''
    },
    mChannel:[],        //主频道接口接收的数据
    subChannel:[],    //子频道接口接收的数据
    subChannelCache:{}    //代理缓存子频道数据
  },
  /**
   * 以键值得到状态值
   * store.getters.test
   */
  getters:{
    test: (state, getters) => {
      return state.test
    },
    mChannel(state, getters){
      return state.mChannel
    },
    subChannel(state, getters){
      return state.subChannel
    },
    subChannelCache(state, getters){
      return state.subChannelCache
    }
  },
  /**
   * 通过this.$store.commit('increment')唤醒一个 mutation handler
   */
  mutations:{
    increment(state) {
      state.test.value++
    },
    mChannel(state,value){
      state.mChannel = value
    },
    subChannel(state,value){
      console.log(value)
      state.subChannel = value
    },
    subChannelCache(state,value){
      for (let v in value){
        state.subChannelCache[v] = value[v]
      }
      console.log(state.subChannelCache)
    },
  },
  /**
   * 通过this.$store.dispatch('INCREMENT')唤醒一个 actions,actions触发一个mutation
   */
  actions:{
    INCREMENT({commit}) {
      commit("increment")
    }
  }
})



