import Vue from 'vue'
/**
 * 时间戳转10 May 2018这种格式
 */
Vue.filter('ddMMMyyyy',function(value){
  let date = new Date(parseInt(value))
  let month = [
    'Jan','Feb','Mar','Apr','May','June','July','Aug','Sep','Oct','Nov','Dec'
  ]
  let y = date.getFullYear();
  let m = date.getMonth();
  m = month[parseInt(m)]
  let d = date.getDate();
  d = d < 10 ? ('0' + d) : d;
  return d+' '+m+' '+y
})

