/**
 * 首字母转大写
 */
Vue.filter('fToUp',function(str){
  if(!str){
    return;
  }
  if(str.indexOf('CRUDEOIL')!=-1){
    str = str.replace('CRUDEOIL', 'CRUDE OIL');
  }
  str = str.toLowerCase();
  let reg = /\b(\w)|\s(\w)/g; //  \b判断边界\s判断空格
  return str.replace(reg,function(m) {
    return m.toUpperCase()
  })
})

/**
 * 自动补0
 */
Vue.filter('supplement0', function(value) {
    if(value.toString().split('.')[1]&&value.toString().split('.')[1].length<=1){
        value+='0'
    }
    if(value.toString().split('.').length == 1){
        value+='.00'
    }
    return value
});
/**
 * 转换日期格式至时间戳
 */
Vue.filter('totimestamp', function(value) {
    return Date.parse(new Date(value))
});
/*
 * useage:金钱过滤器
 * {{123456 | moneyformat(',')}}  ------  123,456
 */
Vue.filter('moneyformat', function(value, s) {
	if(value == null || value == undefined) {
		return value
	}
	if (typeof value == 'number') {
		value = value.toString()
	}
	let arr = value.split('')
	let fh = arr[0] == '-' ? arr.shift() : arr[0] == '+' ? arr.shift() : ''
	let l = arr.length
	l = Math.floor(l / 3)
	for (let i = 1; i <= l; i++) {
		let p = value.length - (3 * i)
		if (p > 0) {
			arr.splice(value.length - (3 * i), 0, s || ',')
		}
	}
	return fh + arr.join('')
});
/*
 * useage:
 * {{ | nullformat}}  ------  -
 */
Vue.filter('nullformat', function(value, s) {
	let ret = value
	if (Object.isEmpty(ret)) {
		ret = !!s?s:'-'
	}
	return ret
});

Vue.filter('nullformat0', function(value, s) {
    let ret = value
    if (Object.isEmpty(ret)) {
        ret = !!s?s:'0'
    }
    return ret
});
/*
 * useage:数字转汉字
 * {{1 | n2cformat}}  ------ 一
 */
Vue.filter('n2cformat', function(value) {
    var chnNumChar = ["零","一","二","三","四","五","六","七","八","九"]
    var chnUnitSection = ["","万","亿","万亿","亿亿"]
    var chnUnitChar = ["","十","百","千"]
	//计算将数字转换成中文
    function SectionToChinese(section){
        var strIns = '', chnStr = '';
        var unitPos = 0;
        var zero = true;
        while(section > 0){
            var v = section % 10;
            if(v === 0){
                if(!zero){
                    zero = true;
                    chnStr = chnNumChar[v] + chnStr;
                }
            }else{
                zero = false;
                strIns = chnNumChar[v];
                strIns += chnUnitChar[unitPos];
                chnStr = strIns + chnStr;
            }
            unitPos++;
            section = Math.floor(section / 10);
        }
        return chnStr;
    }
    function NumberToChinese(num){
        var unitPos = 0;
        var strIns = '', chnStr = '';
        var needZero = false;

        if(num === 0){
            return chnNumChar[0];
        }

        while(num > 0){
            var section = num % 10000;
            if(needZero){
                chnStr = chnNumChar[0] + chnStr;
            }
            strIns = SectionToChinese(section);
            strIns += (section !== 0) ? chnUnitSection[unitPos] : chnUnitSection[0];
            chnStr = strIns + chnStr;
            needZero = (section < 1000) && (section > 0);
            num = Math.floor(num / 10000);
            unitPos++;
        }

        return chnStr;
    }
    return NumberToChinese(value)
});

/**
 * @desc 日期过滤器
 * @author hardy
 * @argument value
 * @argument type
 */
Vue.filter("fmtTime", function(value, type) {
	if (value == undefined || value == null) {
		return void 0
	}
	let dStr = Number.isNumber(value)?value:value.toString().split('-').join('/')
	let date = new Date(dStr);
	let y = date.getFullYear();
	let m = date.getMonth() + 1;
	m = m < 10 ? ('0' + m) : m;
	let d = date.getDate();
	d = d < 10 ? ('0' + d) : d;
	let h = date.getHours();
	h = h < 10 ? ('0' + h) : h;
	let minute = date.getMinutes();
	let second = date.getSeconds();
	minute = minute < 10 ? ('0' + minute) : minute;
	second = second < 10 ? ('0' + second) : second;
	return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second;
})
/**
 * 时分秒
 */
Vue.filter("fmtHms", function(value, type) {
	if (value == undefined || value == null) {
		return void 0
	}
	let dStr = Number.isNumber(value)?value:value.toString().split('-').join('/')
	let date = new Date(dStr);
	let h = date.getHours();
	h = h < 10 ? ('0' + h) : h;
	let minute = date.getMinutes();
	let second = date.getSeconds();
	minute = minute < 10 ? ('0' + minute) : minute;
	second = second < 10 ? ('0' + second) : second;
	return  h + ':' + minute + ':' + second;
})
/**
 * 过滤时间戳为年月日
 */
Vue.filter("fmtDate", function(value, type) {
	if (value == undefined || value == null) {
		return void 0
	}
	let dStr = Number.isNumber(value)?value:value.toString().split('-').join('/')
	let date = new Date(dStr);
	console.log(date)
	let y = date.getFullYear();
	let m = date.getMonth() + 1;
	m = m < 10 ? ('0' + m) : m;
	let d = date.getDate();
	d = d < 10 ? ('0' + d) : d;
	let h = date.getHours();
	h = h < 10 ? ('0' + h) : h;
	let minute = date.getMinutes();
	let second = date.getSeconds();
	minute = minute < 10 ? ('0' + minute) : minute;
	second = second < 10 ? ('0' + second) : second;
    return y + '-' + m + '-' + d ;
})
/**
 * @desc 格式化日期为月日
 * @argument value
 * @argument type
 */
Vue.filter("fmtMonthDay", function(value, type) {
	if (value == undefined || value == null) {
		return void 0
	}
	let dStr = Number.isNumber(value)?value:value.toString().split('-').join('/')
    let date = new Date(dStr);
    let m = date.getMonth() + 1;
    m = m < 10 ? ('0' + m) : m;
    let d = date.getDate();
	d = d < 10 ? ('0' + d) : d;
	return m + '/' + d ;
})

/**
 * @desc 格式化日期为年
 * @argument value
 * @argument type
 */
Vue.filter("fmtYear", function(value, type) {
	if (value == undefined || value == null) {
		return void 0
	}
	let dStr = Number.isNumber(value)?value:value.toString().split('-').join('/')
	let date = new Date(dStr);
	let y = date.getFullYear();
	return y;
})



