import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

function asyncComponent(path) {
    return() =>
        import('@/' + path + '.vue')
}


const router = new Router({
    // mode:'history',,
    // scrollBehavior:()=>({
    //   y:0,
    // }),
    routes: [
        {
            path: '*',
            component: asyncComponent('pages/404'),
            meta:{
                title:'404'
            }
        },
        {
            path: '/edition',
            component: asyncComponent('modules/edition'),
            meta:{
                title:'Edition'
            }
        },
        {
            path: '/',
            component: asyncComponent('pages/InfoMain'),
            redirect:'/home',
            children:[
                {
                    path:'home',
                    component: asyncComponent('modules/home/Home'),
                    meta:{
                        title:'HOME'
                    }
                },
                {
                    path:'/:Mname/:subName',
                    component: asyncComponent('modules/AriticleList'),
                    meta:{
                        title:'newslist'
                    }
                },
                {
                    path: 'prices',
                    component: asyncComponent('modules/price/Prices'),
                    meta:{
                        title:'PRICES'
                    }
                },{
                    path:'news',
                    component:asyncComponent('modules/news/News'),
                    meta:{
                        title:'NEWS'
                    }
                },
                {
                    path: 'data',
                    component: asyncComponent('modules/data/Data'),
                    meta:{
                        title:'DATA'
                    }
                },{
                    path:'analysis',
                    component:asyncComponent('modules/analysis/Analysis'),
                    meta:{
                        title:'ANALYSIS'
                    }
                },
                {
                    path: 'reports',
                    component: asyncComponent('modules/reports/Reports'),
                    meta:{
                        title:'REPORTS'
                    }
                },{
                    path:'about_crudeoil',
                    component:asyncComponent('modules/crudeoil/Crudeoil'),
                    meta:{
                        title:'ABOUT CRUDEOIL'
                    }
                },
                {
                    path:'properties',
                    component:asyncComponent('modules/properties/Properties'),
                    meta:{
                        title:'PROPERTIES'
                    }
                }
            ]
        }

    ]
})

router.beforeEach((to, from, next) => {
    document.title = to.meta.title;

    next();
});


export default router



